.nav__menu {
  display: none;
}

@media (min-width: 550px) {
  .slide-enter {
    transform: translateY(-100%);
    transition: .3s cubic-bezier(0, 1, 0.5, 1);
  }

  .slide-enter-active {
    transform: translateY(0%);
  }


  .slide-leave {
    transform: translateY(0%);
    transition: .3s ease-in-out;
  }

  .slide-leave-active {
    transform: translateY(-100%);
  }

  .submenu-container {
    min-height: 182px;
    min-width: 250px;
    position: absolute;
    overflow: hidden;
    z-index: -1;
  }

  .submenu-activity {
    min-height: 250px;
  }

  .submenu-contact {
    min-height: 100px;
  }

  .menu-container {
    text-align: center;
  }

  nav ul {
    list-style: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
  }

  .nav {
    position: relative;
    display: inline-block;
    color: #fff;
    text-align: left;
    width: 100%;
    border-bottom: 1px solid black;
  }

  .nav a {
    display: block;
    padding: 0 16px;
    line-height: inherit;
    cursor: pointer;
    color: #000;
    text-decoration: none;
    text-shadow: 2px 2px 0px #FFFFFF, 5px 4px 0px rgba(0, 0, 0, 0.15);
  }

  .nav__menu {
    line-height: 45px;
    font-weight: 700;
    text-transform: uppercase;
    display: inline-block;
  }

  .nav__menu-item {
    display: inline-block;
    position: relative;
    border-radius: 3px;
  }

  li.nav__menu-item {
    padding: 0 15px;
    margin: 5px;
  }

  .nav__menu-item:hover {
    -webkit-box-shadow: 0px 0px 18px 1px rgba(158, 158, 158, 0.7);
    -moz-box-shadow: 0px 0px 18px 1px rgba(158, 158, 158, 0.7);
    box-shadow: 0px 0px 18px 1px rgba(158, 158, 158, 0.7);
  }

  .nav__menu-item:hover .nav__submenu {
    display: block;
  }

  .nav__menu-item:hover .submenu-container {
    z-index: 1;
  }

  .nav__submenu {
    font-weight: 300;
    text-transform: none;
    display: none;
    position: absolute;
    width: auto;
    background-color: #fff;
    border: 1px solid black;
  }

  .nav__submenu-item:hover {
    background-color: rgb(243, 243, 243);
  }
}